import React, { Suspense, createContext, useEffect, useState } from 'react'
import { useLocation, useRoutes, useNavigate } from 'react-router-dom'
import UseRouterList from '../../routes'
import { TabBar, SpinLoading } from 'antd-mobile'
import { makeObservable, observable, action, reaction } from 'mobx'
import { HttpGet } from '../../utils/axios'
import './index.css'

class CarStateStore {
    constructor() {
        makeObservable(this, {
            sourceState: observable,
            carState: observable,
            carConfig: observable,
            carColor: observable,
            giveState: action,
            giveCarConfig: action
        })
    }
    //
    sourceState = null
    //
    carState: any = null
    //
    carConfig: any = null
    //
    carColor = null
    //
    giveState = (data: any, witchCar: number) => {
        !data && (data = this.sourceState)
        this.sourceState = data
        this.carState = data?.find((i: any) => i.id === witchCar)
    }
    //
    giveCarConfig = (data: any) => {
        this.carConfig = data
        //
        this.giveCarColor(1)
    }
    //
    giveCarColor = (number: number) => {
        this.carColor = this.carConfig.carColor.find((i: any) => i.id == number)
    }
}

const carStateStore = new CarStateStore()
// const nameList: any = {
//     '/find': '',
//     '/home': 'Tesla',
//     '/map': '',
//     '/shop': '',
//     '/user': ''
// }

reaction(
    () => carStateStore.carState,
    data => {
        if (data) {
            carStateStore.giveCarConfig(data.configuration[0])
        }
    }
)

const Spin = () => {
    return (
        <div className='main_spin'>
            <SpinLoading color='currentColor' style={{ '--size': '48px' }} />
            <div style={{ marginTop: '5px' }}></div>
        </div>
    )
}

export const store = createContext<any>(null)

const Main = () => {
    const [witchCar, setWitchCar] = useState<number>(1)
    const location = useLocation()
    const pathname = location.pathname
    const navigate = useNavigate()
    const element = useRoutes(UseRouterList())
    const tabs = [
        {
            key: '/find',
            title: '',
            icon: pathname !== '/find' ? <img src={require('@/assets/icon/.png')} alt='' className='main_img' /> :
                <img src={require('@/assets/icon/1.png')} alt='' className='main_img_v2' />,
        },
        {
            key: '/map',
            title: '',
            icon: pathname !== '/map' ? <img src={require('@/assets/icon/.png')} alt='' className='main_img' /> :
                <img src={require('@/assets/icon/1.png')} alt='' className='main_img_v2' />,
        },
        {
            key: '/home',
            title: 'Tesla',
            icon: pathname !== '/home' ? <img src={require('@/assets/icon/.png')} alt='' className='main_img' /> :
                <img src={require('@/assets/icon/1.png')} alt='' className='main_img_v2' />,
        },
        {
            key: '/shop',
            title: '',
            icon: pathname !== '/shop' ? <img src={require('@/assets/icon/.png')} alt='' className='main_img2' /> :
                <img src={require('@/assets/icon/1.png')} alt='' className='main_img2_v2' />,
        },
        {
            key: '/user',
            title: '',
            icon: pathname !== '/user' ? <img src={require('@/assets/icon/.png')} alt='' className='main_img2' /> :
                <img src={require('@/assets/icon/1.png')} alt='' className='main_img2_v2' />,
        }
    ]
    useEffect(() => {
        HttpGet('/tesla').then((res: any) => {
            carStateStore.giveState(res.rotationImg, witchCar)
        })
    }, [])
    useEffect(() => {
        carStateStore.giveState(null, witchCar)
    }, [witchCar])
    return (
        <>
            <Suspense fallback={<Spin />}>
                <store.Provider value={{ carStateStore, setWitchCar }}>
                    <div className='main_body'>
                        {element}
                    </div>
                </store.Provider>
            </Suspense>

            <TabBar className='main_tabbar' onChange={value => navigate(value)} defaultActiveKey={pathname}>
                {tabs.map(item => (
                    <TabBar.Item key={item.key} icon={item.icon} title={item.title} />
                ))}
            </TabBar>
        </>
    )
}
export default Main
